"use client";

import React from "react";
import {
  Box,
  Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Button,
  IconButton,
  TextField,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
  Container,
} from "@mui/material";
import EditIcon from "@mui/icons-material/Edit";
import DeleteIcon from "@mui/icons-material/Delete";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";
import Header from "@/components/header/page";
import Footer from "@/components/footer/page";

const ProductPage = () => {
  const [category, setCategory] = React.useState("");

  const handleCategoryChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    setCategory(event.target.value as string);
  };

  return (
    <>
      <Header />
      <Container sx={{ py: 4, minHeight: "calc(100vh - 120px)", color: "#1b1b2f" }}>
        {/* Page Title and Add Button */}
        <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center", mb: 4 }}>
          <Typography variant="h4" sx={{ fontWeight: 600, color: "#1b1b2f" }}>
            All Products
          </Typography>
          <Button
            variant="contained"
            startIcon={<AddCircleOutlineIcon />}
            sx={{
              backgroundColor: "#1f4068",
              color: "#e0e0e0",
              textTransform: "none",
              "&:hover": { backgroundColor: "#162447" },
            }}
          >
            Add Product
          </Button>
        </Box>

        {/* Filters Section */}
        <Box sx={{ display: "flex", gap: 2, mb: 4 }}>
          <TextField
            label="Search Products"
            variant="outlined"
            size="small"
            sx={{
              flex: 1,
              backgroundColor: "#e0e0e0",
              "& .MuiOutlinedInput-root": { borderRadius: "10px" },
            }}
          />
          <FormControl size="small" sx={{ minWidth: 150, backgroundColor: "#e0e0e0", borderRadius: "10px" }}>
            <InputLabel id="category-label">Category</InputLabel>
            <Select labelId="category-label" value={category} onChange={handleCategoryChange}>
              <MenuItem value="electronics">Electronics</MenuItem>
              <MenuItem value="fashion">Fashion</MenuItem>
              <MenuItem value="home">Home</MenuItem>
              <MenuItem value="beauty">Beauty</MenuItem>
            </Select>
          </FormControl>
        </Box>

        {/* Products Table */}
        <TableContainer component={Paper} sx={{ boxShadow: "0 4px 8px rgba(0, 0, 0, 0.15)", borderRadius: 2 }}>
          <Table>
            <TableHead sx={{ backgroundColor: "#1f4068" }}>
              <TableRow>
                <TableCell sx={{ color: "#e0e0e0", fontWeight: 600 }}>Product ID</TableCell>
                <TableCell sx={{ color: "#e0e0e0", fontWeight: 600 }}>Name</TableCell>
                <TableCell sx={{ color: "#e0e0e0", fontWeight: 600 }}>Category</TableCell>
                <TableCell sx={{ color: "#e0e0e0", fontWeight: 600 }}>Price</TableCell>
                <TableCell sx={{ color: "#e0e0e0", fontWeight: 600 }}>Stock</TableCell>
                <TableCell sx={{ color: "#e0e0e0", fontWeight: 600 }}>Actions</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {[...Array(8)].map((_, index) => (
                <TableRow key={index}>
                  <TableCell>#{index + 1}</TableCell>
                  <TableCell>Product Name {index + 1}</TableCell>
                  <TableCell>Category {index % 4}</TableCell>
                  <TableCell>$199.99</TableCell>
                  <TableCell>25</TableCell>
                  <TableCell>
                    <IconButton>
                      <EditIcon sx={{ color: "#1f4068" }} />
                    </IconButton>
                    <IconButton>
                      <DeleteIcon sx={{ color: "#d9534f" }} />
                    </IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Container>
      <Footer />
    </>
  );
};

export default ProductPage;
